@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@200&display=swap);
body {
  margin: 0;
  color: white;
  background: #272727 !important;
  font-family: 'JetBrains Mono', monospace !important;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

i{
  font-size: 25px !important;
  margin: 0 10px 0 10px;
}
.repo-post{
    height: 100px;
    background: #444444;
    padding: 15px;
    width: auto;
    overflow: hidden;
}

.repo-post:hover{
    background: #686868;
}

.repo-post .h4{
    font-weight: bolder;
}

.post-container{
    height: 430px;
}

.post-container a{
    color: white;
} 

.post-container a:hover{
    color: white;
}

